 nav{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 9%;
    padding: 10px 30px;
    transition: .5s;
    background: rgba(0,0,0,0.8);

}
.logo img{
    left: -50px;
	float: left;
}

.logo img:hover{
	opacity: 0.4;
}

.logo img{
    height: 150px;
    width: 150px;
    transition: .5s;
}
nav ul{
    float: inline-start;
    margin: 0;
    padding: -50px;
    display: flex;
}

nav ul li{
    list-style: none;
}

nav ul li .link{
   
    line-height: 40px;
    color: #fff;
    padding: 10px;
    font-family: 'Roboto', sans-serif;
    text-decoration: none;
	text-transform: uppercase;
	font-size: 15px;

}

nav ul li a:active,
nav ul li a:hover{
    color: #fff;
    background: rgba(2, 77, 8, 0.8);
}

/* Change Scroll Position */

nav.black{
    background: rgba(0,0,0.0.8);
    height: 80px;
    padding: 15px 50px;
}

nav.black .logo img{
	width: 110px;
	height: 115px;
}

nav.black ul li .link{
    color: #fff;
    line-height: 45px;
    font-size: 17px;
}
@media (max-width:623px){
    nav{
        padding: 15px;
        width: 100%;
     }
    nav ul li .link{
        padding: 6px;
        font-size: 15px;
    }
    nav.black .logo img{
        width: 80px;
        height: 80px;
    }
    nav.black ul li .link{
        color: #fff;
        padding: 10px;
        line-height: 45px;
        font-size: 15px;
    }
    
}

@media (max-width:453px){
    .logo img{
       display: none;  
    }
    nav{
        padding: 15px;
        width: 100%;
        background: rgba(8, 8, 8, 0.8);
     }

    nav ul li .link{
        line-height: 30px;
        color: #fff;
        padding: 10px;
        font-size: 15px;
    }



     nav.black ul li .link{
        padding: 10px; 
        color: rgb(255, 255, 255);
        line-height: 45px;
        font-size: 15px;
    }
}
.gallery {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr 1fr;  
  }
.gallery-col{
    margin: 25px;
}
.gallery img {
    width: 100%;
    height: auto;
    object-fit: cover;
}

.gallery img:hover {
    transform: scale(1.1);
    opacity: 0.5;
} 

.slide-title {
    padding: 15px;
    text-align: center;
    color: rgb(207, 115, 9);
    text-transform: uppercase;
}

.slide-p{
    text-align: center;
    font-weight: bold;
}

@media (max-width:875px){
    .gallery {
        display: grid;
        grid-template-columns: 1fr 1fr 1fr;  
      }
}

@media (max-width:660px){
    .gallery {
        display: grid;
        grid-template-columns: 1fr 1fr;  
      }
}

@media (max-width:500px){
    .gallery {
        grid-template-columns: 1fr;  
      }
}
.footer-links{

    background: #56ab2f;  /* fallback for old browsers */  /* Chrome 10-25, Safari 5.1-6 */
    background: linear-gradient(to top, #a8e063, #56ab2f); /* W3C, IE 10+/ Edge, Firefox 16+, Chrome 26+, Opera 12+, Safari 7+ */
    
    color: white;
    font-size: 12px;
    padding: 35px 0;

}

.footer-container-page{
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-gap: 10px;
    gap: 10px;
    align-items: flex-start;
    justify-content: center;
}
.footer-container-page ul{
    margin: 0 auto;
    list-style:none;
}

.footer-container-page h5{
    color: #343A40;
}

.footer-container-page ul li{
    line-height: 2.8;

}

.footer-container-page .icons-social{
    font-size: 1.2rem;
}
.footer-development{
    background: #343A40;
    color: white;
    padding: 3px 0;
    text-align: center;
}

/* CONTACT WHATSAPP*/

.float{
	position:fixed;
	width:50px;
	height:50px;
	bottom:40px;
	right:40px;
	background-color:#25d366;
	color:#FFF;
	border-radius:50px;
	text-align:center;
    font-size:30px;
	box-shadow: 2px 2px 3px #999;
  z-index:100;
}
.float:hover {
	text-decoration: none;
	color: #25d366;
    background-color:#fff;
}

.my-float{
	margin-top:auto;
}

@media (max-width:700px){
   
    .footer-container-page {
        text-align: center;
        grid-template-columns: repeat(2, 1fr);
    }
    .footer-container-page ul{
        list-style:none;
    }
}

@media (max-width:500px){

    .footer-container-page ul{
    list-style:none;
    }

    .footer-container-page{
        text-align: center;
        grid-template-columns: 1fr ;
    }

}
*{
